import React from 'react';
import PropTypes from 'prop-types';

import Aside from '../Aside';
import Content from '../../containers/Content';

const Main = ({
  pickedGenre,
  genreLoading,
}) => {
  let render;
  if (genreLoading) {
    render = (
      <div className="empty content flex-c">
        <div className="loading-wrap">
          <div className="loading-bar-0" />
          <div className="loading-bar-1" />
          <div className="loading-bar-2" />
          <div className="loading-bar-3" />
        </div>
      </div>
    );
  } else if (pickedGenre && pickedGenre.length > 0) {
    render = (
      <Content genre={pickedGenre} />
    );
  } else {
    render = (
      <div className="empty content flex-c">
        <div className="empty-text">Enter a excel and pick a genre to start the game.</div>
      </div>
    );
  }

  return (
    <div className="main frame-item">
      <Aside />
      {render}
    </div>
  );
};

Main.propTypes = {
  genreLoading: PropTypes.bool.isRequired,
  pickedGenre: PropTypes.string.isRequired,
};

export default Main;



