import { fromJS } from 'immutable';

import googleAppScript from '@/api/googleAppScript';
// import fakeData from '@/fakeData/songs';

import {
  onGenreChange,
  onGenreReset,
} from './genreAction';
import {
  onSpinnerGenreHide,
  onSpinnerGenreShow,
} from '../spinnerAction';
import {
  onNavToggle,
} from '../navAction';

const onGenrePicked = (excel, genre) => (
  (dispatch) => {
    function success(res) {
      const { data } = res;

      if (data) dispatch(onGenreChange(genre, fromJS(data)));
      else alert('Ajax got nothing.');

      dispatch(onSpinnerGenreHide());
    }

    function failed(err) {
      console.error(err);
      alert('Can\'t get datas from Google Spread Sheet. Please check the excel ID or click the \'auth\' button to get access then try again.');
    }

    dispatch(onSpinnerGenreShow());
    dispatch(onGenreReset());
    dispatch(onNavToggle(false));

    googleAppScript({
      excel,
      action: 'getGenreSongs',
      name: genre,
      quantity: 10,
    })
      .then(success)
      .catch(failed);
  }
);

export default onGenrePicked;



