import qs from 'querystring';
import jsonp from 'jsonp';

function jsonpPromise(url, params) {
  let fetchUrl = url;

  if (params) {
    fetchUrl = `${url}?${qs.stringify(params)}`;
  }
  console.log(fetchUrl);

  return new Promise((resolve, reject) => {
    jsonp(fetchUrl, (err, data) => {
      console.log(err);
      console.log(data);
      if (err) {
        reject(err);
      } else {
        resolve(data);
      }
    });
  });
}

export default jsonpPromise;
export const jsonpCurry = url => params => jsonpPromise(url, params);



